<template>
  <transition transition name="fade" mode="out-in">
    <div v-if="show" class="w3-modal w3-black modal-show" :key="img_in">
      <span
        class="
          w3-text-white
          w3-xxlarge
          w3-hover-text-grey
          w3-container
          w3-display-topright
        "
        v-on:click="onClose()"
        >×</span
      >
      <span
        class="
          w3-text-white
          w3-xxlarge
          w3-hover-text-grey
          w3-container
          w3-display-bottomleft
        "
        v-on:click="onShowPrev()"
        >❮</span
      >
      <span
        class="
          w3-text-white
          w3-xxlarge
          w3-hover-text-grey
          w3-container
          w3-display-bottomright
        "
        v-on:click="onShowNext()"
        >❯</span
      >
      <span
        class="
          w3-text-white
          w3-xxlarge
          w3-hover-text-grey
          w3-container
          w3-display-bottommiddle
        "
        v-on:click="onPause()"
        v-if="isAnimating"
        >&#10074;&#10074;</span
      >
      <span
        class="
          w3-text-white
          w3-xxlarge
          w3-hover-text-grey
          w3-container
          w3-display-bottommiddle
        "
        v-on:click="onPlay()"
        v-if="!isAnimating"
        >&#9205;</span
      >
      <transition transition name="fade" mode="out-in">
        <img :src="imgSrc" :key="imgSrc" />
      </transition>
    </div>
  </transition>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { GImg } from "../database/database";

export default defineComponent({
  name: "SlideShow",
  props: {
    img_in: {
      type: Object as PropType<GImg>,
      required: true,
    },
    imgs: {
      type: Array as PropType<GImg[]>,
      required: true,
    },
  },
  data() {
    return {
      img: this.img_in as GImg,
      timer_anim_handle: null as number | null,
    };
  },
  computed: {
    imgSrc(): string | null {
      return this.img ? this.img.pathBig : null;
    },
    show(): boolean {
      return this.img != null;
    },
    isAnimating(): boolean {
      return this.timer_anim_handle != null;
    },
  },
  watch: {
    img_in() {
      this.img = this.img_in;
      this._prepare();
    },
  },
  methods: {
    onClose() {
      this.$emit("hide_slide_show");
    },
    onAnimToggle() {
      this._setAnimEnabled(!this.timer_anim_handle, true);
    },
    onShowPrev() {
      this._setImage(-1);
    },
    onShowNext() {
      this._setImage(1);
    },
    onPause() {
      this._setAnimEnabled(false, true);
    },
    onPlay() {
      this._setAnimEnabled(true, true);
    },
    _setImage(add: number) {
      var idx = this.imgs.findIndex((val) => val === this.img);
      if (idx !== -1) {
        let i1 = idx + add;
        const maxI = this.imgs.length;
        while (i1 >= maxI) {
          i1 -= maxI;
        }
        while (i1 < 0) {
          i1 += maxI;
        }
        this.img = this.imgs[i1];

        if (this.timer_anim_handle != null) {
          clearInterval(this.timer_anim_handle);
          this.timer_anim_handle = setInterval(this.onShowNext, 4000);
        }
      }
    },
    _prepare() {
      this._setAnimEnabled(this.show, false);
    },
    _setAnimEnabled(enable: boolean, quick: boolean) {
      if (this.timer_anim_handle) {
        if (!enable) {
          clearInterval(this.timer_anim_handle);
          this.timer_anim_handle = null;
        }
      } else {
        if (enable) {
          this.timer_anim_handle = setInterval(
            this.onShowNext,
            quick ? 16 : 4000
          );
        }
      }
    },
  },
  mounted() {
    this._prepare();
  },
});
</script>

<style scoped lang="scss">
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.2s ease;
}
.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}
.modal-show {
  display: block;
  padding: 54px 0 54px 0;
}
.modal-show span {
  cursor: pointer;
  user-select: none;
}
.modal-show img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
</style>
