<template>
  <div v-if="show">
    <div class="w3-container w3-margin-bottom w3-margin-top">
      <h3 class="w3-border-bottom w3-border-grey">{{ gallery_ob.title }}</h3>
      <a v-if="show_download_all" v-bind:href="gallery_ob.download_all"
        >Download</a
      >
    </div>
    <div class="w3-row-padding" v-for="row in rows" :key="row.uuid">
      <div
        class="w3-col l3 m6 w3-margin-bottom gal-item"
        v-for="item in row"
        :key="item.uuid"
      >
        <div
          class="w3-round-large w3-border w3-border-dark-grey"
          v-on:click="onShowSlideShow(item)"
        >
          <img :src="item.pathSmall" alt="" class="w3-display-middle" />
        </div>
      </div>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, PropType } from "vue";
import { GObj, GImg } from "../database/database";

export default defineComponent({
  name: "Slides",
  props: {
    gallery_ob: {
      type: Object as PropType<GObj>,
    },
  },
  data() {
    return {
      imgs: [] as GImg[],
    };
  },
  computed: {
    rows(): GImg[][] {
      return this._getChunks(this.imgs, 4);
    },
    show(): boolean {
      return this.gallery_ob != null && this.imgs.length > 0;
    },
    show_download_all(): boolean {
      return this.gallery_ob != null && this.gallery_ob.download_all != null;
    },
  },
  watch: {
    gallery_ob() {
      this.imgs = [];
      this._maybeGetImgs();
    },
  },
  methods: {
    onShowSlideShow(item: GImg) {
      this.$emit("show_slide_show", { img: item, imgs: this.imgs });
    },
    _maybeGetImgs() {
      if (this.gallery_ob != null && this.imgs?.length === 0) {
        // timeout to simulate request
        setTimeout(() => {
          this.imgs = this.gallery_ob?.getImgs() ?? [];
        }, 16);
      }
    },
    _getChunks(arr: GImg[], size: number): GImg[][] {
      return arr.reduce(
        (acc, _, i) => (i % size ? acc : [...acc, arr.slice(i, i + size)]),
        [] as GImg[][]
      );
    },
  },
  mounted() {
    this._maybeGetImgs();
  },
});
</script>

<style scoped lang="scss">
h3 {
  padding-bottom: 0 !important;
}
.gal-item img {
  max-width: 100%;
  max-height: 100%;
  padding: 16px;
}
.gal-item div {
  height: 250px;
  position: relative;
}
</style>
