/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
<template>
  <div>
    <template v-for="obI in gallery_obs" :key="obI.id">
      <Slides :gallery_ob="obI" @show_slide_show="onShowSlideShow" />
    </template>
    <SlideShow
      :img_in="show_img"
      :imgs="show_imgs"
      @hide_slide_show="onHideSlideShow"
    />
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

import SlideShow from "@/components/SlideShow.vue";
import Slides from "@/components/Slides.vue";
import { page_obs, GPage, GObj, GImg } from "../database/database";

export default defineComponent({
  name: "Gallery",
  props: {
    gall_id: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      gallery_obs: [] as GObj[],
      show_img: null as GImg | null,
      show_imgs: [] as GImg[],
    };
  },
  watch: {
    gall_id() {
      this.loadGall(this.gall_id);
    },
  },
  methods: {
    onShowSlideShow(obj: { img: GImg; imgs: GImg[] }) {
      this.show_img = obj.img;
      this.show_imgs = obj.imgs;
      document.body.classList.add("modal-open");
    },
    onHideSlideShow() {
      this.show_img = null;
      this.show_imgs = [];
      document.body.classList.remove("modal-open");
    },
    loadGall(gall_id: string) {
      const gall =
        gall_id && page_obs
          ? page_obs.find((elI: GPage) => elI.id == gall_id)
          : null;
      this.gallery_obs = gall?.gallery_obs ?? [];
    },
  },
  created() {
    this.loadGall(this.gall_id);
  },
  components: {
    Slides,
    SlideShow,
  },
});
</script>
