
import { defineComponent, PropType } from "vue";
import { GObj, GImg } from "../database/database";

export default defineComponent({
  name: "Slides",
  props: {
    gallery_ob: {
      type: Object as PropType<GObj>,
    },
  },
  data() {
    return {
      imgs: [] as GImg[],
    };
  },
  computed: {
    rows(): GImg[][] {
      return this._getChunks(this.imgs, 4);
    },
    show(): boolean {
      return this.gallery_ob != null && this.imgs.length > 0;
    },
    show_download_all(): boolean {
      return this.gallery_ob != null && this.gallery_ob.download_all != null;
    },
  },
  watch: {
    gallery_ob() {
      this.imgs = [];
      this._maybeGetImgs();
    },
  },
  methods: {
    onShowSlideShow(item: GImg) {
      this.$emit("show_slide_show", { img: item, imgs: this.imgs });
    },
    _maybeGetImgs() {
      if (this.gallery_ob != null && this.imgs?.length === 0) {
        // timeout to simulate request
        setTimeout(() => {
          this.imgs = this.gallery_ob?.getImgs() ?? [];
        }, 16);
      }
    },
    _getChunks(arr: GImg[], size: number): GImg[][] {
      return arr.reduce(
        (acc, _, i) => (i % size ? acc : [...acc, arr.slice(i, i + size)]),
        [] as GImg[][]
      );
    },
  },
  mounted() {
    this._maybeGetImgs();
  },
});
